@use "global" as *;

.swiper-gallery-photo {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(1);
    // アニメーションの設定
    // animation: animation-mv 80s linear 0s 1 normal both; // 一括指定のスタイル
    animation-name: animation-gallery; // アニメーション名
    animation-duration: 8s; // アニメーション1回分の時間
    animation-timing-function: linear; // アニメーションの進行スタイル
    animation-delay: 0s; // アニメーションが開始するまでの時間
    animation-direction: normal; // アニメーションの再生方向
    animation-fill-mode: both; // アニメーション実行前後の状態

    @keyframes animation-gallery {
        0% {
            transform: translate(-50%, -50%) scale(1);
            // 
        }

        100% {
            transform: translate(-50%, -50%) scale(1.15);
            // 
        }
    }

    // 
}